
html {
  box-sizing: border-box;
  background: url("../static/bg-img.jpg") center no-repeat;
  background-size: cover;
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: center;
  font-family: Futura, "Trebuchet MS", Arial, sans-serif;
}
svg {
  fill: #fff;
  background: rgba(0,0,0,0.1);
  padding: 20px;
  border-radius: 50%;
  width: 200px;
  margin-bottom: 50px;
}
#app {
  max-height: 550px;
  background: rgba(255,255,255,0.95);
  box-shadow: 0 0 0 10px rgba(0,0,0,0.1);
  position: relative;
  width: 270px;
  margin: auto;
}
h2 {
  text-align: center;
  margin: 0;
  font-weight: 200;
}
.list-items {
  padding: 0 20px;
  margin: 0;
  text-align: left;
  list-style: none;
  height: 480px;
  overflow: auto;
}
.list-items li {
  border-bottom: 1px solid rgba(0,0,0,0.2);
  padding: 10px 0;
  font-weight: 100;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.list-items label {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  cursor: pointer;
}
.list-items label > span {
  float: right;
  display: inline-block;
  margin-top: 3px;
}
.list-items input {
  display: none;
}
.list-items input + label:before {
  content: '⬜️';
  margin-right: 10px;
}
.list-items input:checked + label:before {
  content: '❤️';
}
.add-items {
  margin-top: 20px;
  padding: 5px;
  position: absolute;
  bottom: 0;
  width: 260px;
  background: #eaeff0;
  height: 45px;
}
.add-items input {
  padding: 10px;
  margin: 5px 0;
  outline: 0;
  border: 1px solid rgba(0,0,0,0.1);
}


